.react-app{
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-size: 16px;
  color: white;
}

.react-app-header,
.react-app-footer{
  background-color: black;
  text-align: center;  
  width: 100%;
}

.react-app-header{
  position: sticky;
  top: 0;
}

.react-app-footer{
  position: fixed;
  bottom: 0;
}

.react-app-header-text{
  font-size: 30px;
}

.react-app-footer-text{
  font-size: 24px;
}

.App-logo {
  height: 6vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.react-app-components{
  /* padding: 20px 20px 200px 20px; */
}


.react-app-classComponents-header,
.react-app-functionalComponents-header{
  font-size: 24px;
  font-weight: 600;
}

.react-app-classComponents-subheader,
.react-app-functionalComponents-subheader{
  font-size: 20px;
}


/***Top Nav Bar changes starts **/
.top-nav-bar{
  width: 100%;
  background: black;
}

.top-nav-bar ul li{
  list-style-type: none;
  display: inline-block;
  padding: 15px;
}

.top-nav-bar ul li:hover{
  background: grey;
}

.top-nav-bar ul li .top-nav-bar-links{
  text-decoration: none;
  color: #999999;
  font-weight: 600;
}

.top-nav-bar ul li:hover .top-nav-bar-links{
color: white;

}

.top-nav-dropdown{
  position: absolute;
  box-shadow: 0 0 4px 2px(0,0,0.05);
  border: 1px solid #d0d0d0;
  background: white;
  border-radius: 4px;
}

.top-nav-dropdown .top-nav-dropdown-links{
  display: block;
  padding: 10px 20px;
  color: grey !important;
  text-decoration: none;
}

.top-nav-dropdown .top-nav-dropdown-links:hover{
  background: lightgray;
  color: white  !important;
}

.top-nav-bar ul li:hover .top-nav-dropdown{
  display: block !important;
}
/***Top Nav Bar changes Ends **/



